* {
  box-sizing: border-box;
}

body,
ul,
li {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}
/* ^^^^^^^^^^^Reset styles^^^^^^^^^^^ */

.block-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.block {
  --block-height: 300px;

  min-width: var(--block-height);
  height: var(--block-height);
  flex-basis: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 100px;
  color: #fff;

}

@media (min-width: 600px) {
  .block {
    flex-basis: 50%;
  }

  .block--3 {
    order: 4;
  }

  .block--4 {
    order: 3;
  }

  .block--5 {
    order: 5;
  }

  .block--6 {
    order: 6;
  }
}

@media (min-width: 900px) {
  .block {
    flex-basis: 33.3%;
  }

  .block--3 {
    order: 3;
  }

  .block--4 {
    order: 6;
  }

  .block--6 {
    order: 4;
  }
}

.block--1 {
  background-color: #f00;
}

.block--2 {
  background-color: #c00;
}

.block--3 {
  background-color: #900;
}

.block--4 {
  background-color: #600;
}

.block--5 {
  background-color: #300;

}

.block--6 {
  background-color: #000;
}
